@import "styles/variables.scss";

.modalContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(35, 35, 35, 0.7);
    z-index: 1000000;
}

.modal {
    min-width: 350px;
    width: fit-content;
    position: relative;
    background-color: $darkBlue;

    &.light {
        background-color: $beige;
    }
}

.modal > img {
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
}