@import "styles/variables.scss";

.headerActions {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.headerActions > a {
    margin-right: 60px;
}

.headerActions > a > button {
    text-decoration: underline;
}

.white {
    color: $beige;
}

.headerActions button {
    background-color: transparent;
    text-transform: uppercase;
    cursor: pointer;
    width: 80px;
}

.iconGroup {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.iconGroup > a {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
}

.iconGroup > div.search > img {
    display: block;
}

.iconGroup > a:not(:last-child),
.iconGroup > div.search {
    margin-right: 10px;
}

.iconGroup > div.search > img {
    cursor: pointer;
}

.checkoutCount {
    font-size: 10px;
    position: relative;
    bottom: 5px;
    left: 3px;
}