.MobileHeader {
    display: flex;
    justify-content: space-between;
    height: 48px;
}

.leftBtnGrp {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.leftBtnGrp > button {
    margin: 0 14px 0 12px;
    background-color: transparent;
    text-transform: uppercase;
    cursor: pointer;
    font-size: 16px;
}

.leftBtnGrp > a {
    position: relative;
    top: 3px;
}

.rightBtnGrp {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.rightBtnGrp > a {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
}

.rightBtnGrp > a:not(:first-child) {
margin: 0 12px 0 8px;
}

.checkoutCount {
    font-size: 10px;
    position: relative;
    bottom: 5px;
    left: 3px;
}