@import "styles/variables.scss";

.nav {
    display: flex;
    justify-content: space-between;
}

.nav>button {
    background-color: transparent;
    text-transform: uppercase;
    cursor: pointer;
}

.nav>button:not(:last-child) {
    padding-right: 32px;
}

.white>button {
    color: $beige;
}

.lkAndTranslate {
    display: none;
    justify-content: space-between;
    padding: 60px 24px;
    width: calc(100% - 48px);
}

.lkAndTranslate>a>button {
    font-size: 16px;
    text-transform: uppercase;
}

.lkAndTranslate>button:last-child {
    text-decoration: underline;
}

.isGradient {
    position: relative;
    z-index: -1;
    mask-image: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, #F3F3EA 15%, #F3F3EA 85%, rgba(0, 0, 0, 0) 100%);
}

.gradientLeft {
    z-index: -1;
    position: relative;
    mask-image: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, #F3F3EA 15%, #F3F3EA 100%);
}

.gradientRight {
    z-index: -1;
    position: relative;
    mask-image: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, #F3F3EA 0%, #F3F3EA 85%, rgba(0, 0, 0, 0) 100%);
}

@media #{$tablet-less} { 
    .nav>button {
        font-size: 15px;

        &.active {
            opacity: 100%;
        }
    }
    
}

@media (max-width: 1180px) { 
    .nav>button {
        user-select: none;
        font-size: 14px;

        &.active {
            opacity: 100%;
        }
    }

    .nav>button:last-child {
        padding-right: 16px;
    }
}

@media (max-width: 1120px) { 
    .nav>button {
        user-select: none;
        font-size: 14px;

        &.active {
            opacity: 100%;
        }
    }

    .nav>button:not(:last-child) {
        padding-right: 24px;
    }

    .nav>button:last-child {
        padding-right: 16px;
    }
}


