    }
 
}

@media (min-width: 896px) and (max-width: 962px) { 
    .nav>button {
       user-select: none;
        font-size: 12px;

        &.active {
            opacity: 100%;
        }
    }

    .nav>button:not(:last-child) {
        padding-right: 16px;
    }
 
}


@media (min-width: 849px) and (max-width: 895px) { 
