@media #{$mobile} {
    .lkAndTranslate {
        display: none;
    }

    .line {
        width: 100vw;
        position: absolute;
        left: 0;
        right: 0;
    }

    .nav {
        margin: 60px auto 0;
        justify-content: flex-start;
        margin-left: 16px;
        margin-right: 16px;
        gap: 16px;
        overflow-x: auto;
        -ms-overflow-style: none;
        scrollbar-width: none;
        position: relative;
    }
    
    .nav::-webkit-scrollbar {
        display: none;
        -webkit-appearance: none;
        width: 0;
        height: 0;
    }

    .nav>button {
        padding: 16px 0;
        color: $beige;
        opacity: 48%;
        user-select: none;
        font-size: 16px;

        &.active {
            opacity: 100%;
        }
    }

    .nav>button:not(:last-child) {
        padding-right: 0;
    }
}