@import "styles/variables.scss";

.HeaderBanner {
    width: fit-content;
    height: fit-content;
    position: relative;
}

.notHeader {
    width: 100%;
    overflow: hidden;
    cursor: pointer;
}

.aboutKids {
    cursor: default !important;
}

.customBlock {
    pointer-events: none;
}

.about_btn {
    width: 260px;
    height: 64px;
    margin-top: 40px;


    pre {
        font-size: 24px !important;
        font-weight: 450;
        line-height: 32px;
        text-align: center;
    }
}

.notHeader>img,
.notHeader>video {
    position: relative !important;
    width: 100% !important;
    height: auto !important;
	object-fit: cover;
}

.notHeader>video {
    aspect-ratio: 136/64;
    object-fit: cover;
}

.HeaderBanner.notHeader>a {
    position: absolute;
    bottom: 35px;
    left: 23px;
    cursor: pointer;
    text-decoration: none;
}

.HeaderBanner.notHeader>a>button,
.HeaderBanner>a>button {
    cursor: pointer;
}

.HeaderBanner>a {
    position: absolute;
    bottom: 16px;
    left: 16px;
    cursor: pointer;
    text-decoration: none;
}

.wholeSale {
    position: absolute;
    bottom: 25px;
    left: 38px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    position: relative;
    background-color: transparent;
    max-width: 513px;
}

.wholeSale.isBlackBtn {
    border: 1px solid $darkBlue;
}

.wholeSale>p:first-child {
    text-align: left;
    color: var(--Beige, #F3F3EA);
    font-size: 80px;
    font-style: normal;
    font-weight: 450;
    line-height: 93%;
    letter-spacing: -2.4px;
    padding: 16px 0px 34px 16px;
}

.wholeSale>img:last-child {
    margin-bottom: 14px;
}

.headerButton {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    position: relative;
    background-color: transparent;
    padding: 16px;
    max-width: 513px;
}

.headerButton.isBlackBtn {
    border: 1px solid $darkBlue;
}

.headerButton :nth-child(2) {
    position: absolute !important;
    right: -5px;
    bottom: 3px;
}

.headerButton>p:first-child {
    text-align: left;
    color: var(--Beige, #F3F3EA);
    font-size: 80px;
    font-style: normal;
    font-weight: 450;
    line-height: 93%;
    letter-spacing: -2.4px;
}

.headerButton>p:last-child {
    position: absolute;
    top: 135px;
    right: -63px;
    color: $beige;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%;
    /* 20px */
    letter-spacing: -0.4px;
    text-transform: uppercase;

    &>* {
        font-size: 16px;
    }
}

.headerButton.isBlackBtn>p:last-child {
    color: $darkBlue;
}

.bread_kids {
    position: absolute;
    top: 56px;
    left: 80px;
    cursor: pointer;
}

.about_kids {
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 24px;
    max-width: 50%;
    align-items: flex-start;
    left: 80px;
    bottom: 383px;


    .title_text {
        font-size: 48px;
        font-weight: 450;
        line-height: 56px;
        letter-spacing: 0em;
        text-align: left;
    }

    .bottom_text {
        font-size: 20px;
        line-height: 28px;
    }
}

.isAllWidth {
    width: 100vw;
    transform: translateX(-40px);
}

@media screen and (min-width: 1681px) {
    .isAllWidth {
        width: 100%;
        transform: translateX(0);
    }
}

@media #{$tablet} {
    .notHeader {
        position: relative;
        margin-bottom: 0;
    }

    .notHeader>video {
        aspect-ratio: 39/62;
    }

    .wholeSale {
        width: 356px;
        bottom: 25px;
        left: 0px;
    }

    .wholeSale>p:first-child {
        text-align: left;
        color: var(--Beige, #F3F3EA);
        font-size: 80px;
        font-style: normal;
        font-weight: 450;
        line-height: 93%;
        /* 74.4px */
        letter-spacing: -2.4px;
        padding: 24px 0px 24px 12px;
    }

    .wholeSale>img:last-child {
        width: 70px;
        margin-bottom: -10px;
    }

    .headerButton {
        width: calc(100% - 30px);
        max-width: none;
        padding: 20px;
    }

    .HeaderBanner.notHeader>a,
    .HeaderBanner>a {
        bottom: 15px;
        left: 15px;
        width: 100%;
        background-color: transparent;
    }

    .headerButton>p:first-child {
        max-width: 305px;
        font-size: 56px;

        &>* {
            font-size: 56px;
        }
    }

    .headerButton>p:last-child {
        font-size: 16px;
        top: 10px;
        right: 10px;

        &>* {
            font-size: 16px;
        }
    }

    .isAllWidth {
        width: 100vw;
        transform: translateX(-12px);
    }

    .headerButton:hover {
        background-color: inherit;
    }

    .headerButton:hover :nth-child(2) img {
        fill: inherit;
    }

    .headerButton:hover>p:first-child,
    .headerButton:hover>p:last-child {
        color: $beige;
    }

    .headerButton :nth-child(2) {
        transform: translate(-3px, -2px)
    }
}


@media #{$less-desc} {
    .about_kids {
        position: absolute;
        display: flex;
        flex-direction: column;
        gap: 24px;
        max-width: 80%;
        align-items: flex-start;
        left: 80px;
        top: 103px;


        // .title_text {
        //     font-size: 38px;
        //     font-weight: 450;
        //     line-height: 48px;
        //     letter-spacing: 0em;
        //     text-align: left;
        // }

        // .bottom_text {
        //     font-size: 12px;
        //     line-height: 18px;
        // }
    }
}

@media #{$tablet} {
    .about_kids {
        position: absolute;
        display: flex;
        flex-direction: column;
        gap: 24px;
        max-width: 80%;
        align-items: flex-start;
        left: 20px;
        top: 103px;


        .title_text {
            font-size: 38px;
            font-weight: 450;
            line-height: 48px;
            letter-spacing: 0em;
            text-align: left;
        }

        .bottom_text {
            font-size: 12px;
            line-height: 18px;
        }
    }

    .bread_kids {
        left: 20px;
    }


.about_btn {
    width: 160px;
    height: 64px;
    margin-top: 20px;


    pre {
        font-size: 18px !important;
        font-weight: 450;
        line-height: 26px;
        text-align: center;
    }
}

}

@media #{$smaller-se} {
    .headerButton>p:first-child {
        font-size: 46px;

        &>* {
            font-size: 46px;
        }
    }
}
