@import "styles/variables.scss";

.actionBtnContainer {
    display: flex;
    justify-content: flex-start;
    width: 100%;
}

.actionBtn {
    cursor: pointer;
    background-color: #1B2530;
    font-size: 22px;
    color: #F3F3EA;
    line-height: 100%;
    padding: 17px 44px;
    width: 100%;
    max-width: 418px;
    text-transform: uppercase;
}

.col {
    position: relative;
}

.col>.banner {
    margin-bottom: 0;
    position: relative;
}

.banner::after {
    content: '';
    position: absolute;
    display: inline-block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    background: linear-gradient(180deg, rgba(27, 37, 48, 0.00) 19.5%, #1B2530 100%);
    background-position: 0px 200px;
    background-repeat: no-repeat;
}

.banner a {
    z-index: 2;
}

.banner button {
    max-width: 617px;
    border: 2px solid #F3F3EA;
}

.banner p {
    font-size: 70px !important;
}

div.banner {
    transform: translateX(0px);
}

button.bannerBtn {
    position: absolute;
    bottom: 40px;
    right: 60px;
    width: 320px;
    height: auto;
}

.text {
    color: var(--Dark-Blue, #1B2530);
    max-width: 1115px;
    font-size: 25px;
    font-style: normal;
    font-weight: 450;
    line-height: 126%;
    letter-spacing: -0.25px;
    margin-bottom: 73px;
}

.text:last-of-type {
    margin-bottom: 105px;
}

.favorite {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 51.38px;
}

.assorti {
    display: flex;
    flex-direction: column;
    margin-bottom: 87px;
}

.assorti p {
    font-size: 22px;
    line-height: 126%;
    letter-spacing: -0.223px;
}

.assorti>.col {
    display: flex;
    flex-direction: column;
    font-style: normal;
    font-weight: 450;
    line-height: 126%;
    letter-spacing: -0.223px;
    gap: 16px;
    max-width: 1173px;
    margin-left: 10px;

    &>div {
        display: flex;
        align-items: flex-start;
    }
}

.assorti img {
    position: relative;
    top: -10px;
    margin-right: 8px;
    margin-bottom: 12px;
}

.favorite>.col {
    display: flex;
    flex-direction: column;

    &>div {
        display: flex;
        align-items: flex-start;
    }
}

.favorite img {
    position: relative;
    top: -10px;
    margin-right: 8px;
    margin-bottom: 12px;
}

.favorite p {
    font-size: 22px;
    line-height: 126%;
    letter-spacing: -0.223px;
    margin-bottom: 12px;
}

.title {
    color: var(--Dark-Blue, #1B2530);
    font-size: 60px;
    font-style: normal;
    font-weight: 450;
    line-height: 110%;
    letter-spacing: -1.8px;
}

.favoriteImg {
    width: 100vw;
    height: auto;
    position: relative !important;
}

.orient {
    font-size: 60px;
    font-style: normal;
    font-weight: 450;
    line-height: 126%;
    letter-spacing: -0.6px;
}

.check {
    padding: 18.113px 27.867px 15.327px 27.867px;
    width: 418px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 2.787px;
    background: #1B2530;
    flex-shrink: 0;
    color: var(--Beige, #F3F3EA);
    font-size: 22.293px;
    font-style: normal;
    font-weight: 450;
    line-height: 22.293px;
    text-transform: uppercase;
    text-align: center;
}

.check:hover {
    cursor: pointer;
}

.middleBanner {
    display: flex;
    align-items: center;
    max-width: 1080px;
    padding: 25px;
    margin: 0 auto;
    border: 1px solid rgba(35, 35, 35, 0.3);
    margin-bottom: 135px;


    &>div {
        display: flex;
        flex-direction: column;
        gap: 34px;
    }
}

.middleBanner img {
    margin-right: 29px;
}

.middleBanner p {
    font-size: 35.912px;
    line-height: 126%;
    letter-spacing: -0.359px;
}

.conditions {
    margin-bottom: 150px;
    display: flex;
    flex-direction: column;
    gap: 33px;
}

.conditions>div {
    display: flex;
    align-items: baseline;
}

.conditions>div>p:first-child {
    font-size: 32px;
    line-height: 110%;
    letter-spacing: -0.64px;
    min-width: 302px;
    display: inline-block;
}

.conditions>div>p:last-child {
    justify-content: flex-end;
    font-size: 48px;
    line-height: 110%;
    letter-spacing: -1.46px;
}

.imgList {
    display: flex;
    margin-bottom: 93px;
}

.imgList>img {
    width: 31.52%;
    max-width: 360px;
    height: auto;
}

.imgList>img:not(:last-child) {
    margin-right: 31px;
}

.subTitle {
    max-width: 950px;
}

.text:last-of-type.disclaimer {
    margin-bottom: 74px;
}

.assortment {
    margin-bottom: 119px;
    font-size: 29.208px;
    line-height: 126%;
    letter-spacing: -0.292px;
}

.assortment>a {
    text-decoration: none;
    border-bottom: 1px solid $darkBlue;
    font-size: 29.208px;
    line-height: 126%;
    letter-spacing: -0.292px;
}

.emailBlock {
    display: flex;
    flex-direction: column;
    background-color: $darkBlue;
    padding: 72px 34px 56px;
    margin-top: 393px;
    transform: translateY(-300px);
}

.emailBlock>p {
    font-size: 64.92px;
    line-height: 93%;
    letter-spacing: -1.948px;
    margin-bottom: 78px;
    color: $beige;
}

.emailBlock>div {
    display: flex;
    align-items: center;
    width: 100%;
}

.modalContainer {
    padding: 78px 58px 98px 34px;
}

.emailBlock>div>div.emailInput,
div.modalContent>div.emailInput,
.emailBlock>div>input.emailInput,
div.modalContent>input.emailInput {
    width: 33%;
    background-color: $darkBlue;
    height: 50px;
    border: none;
    border-bottom: 1px solid $beige;
    color: #F3F3EB;
    font-variant-numeric: slashed-zero;
    font-size: 24.258px;
    font-style: normal;
    font-weight: 400;
    line-height: 125%;
    /* 30.322px */
    letter-spacing: -0.485px;
}

div.modalContent>div.emailInput,
div.modalContent>input.emailInput {
    width: 100%;
    margin-bottom: 42px;
}

.emailBlock>div>div.emailInput>input,
div.modalContent>div.emailInput>input {
    background-color: $darkBlue;
    height: 50px;
    border-bottom: 1px solid $beige;
    padding: 0;
    color: $beige;
    font-size: 24.258px;
    line-height: 125%;
    letter-spacing: -0.485px;
}

.emailBlock>div input::placeholder,
div.modalContent input::placeholder {
    color: $beige;
    opacity: 0.4;
}

.emailBlock>div>*:not(:last-child),
div.modalContent>*:not(:last-child) {
    margin-right: 57px;
}

.emailBlock>div>img,
div.modalContent>div.sendBlock>img {
    cursor: pointer;
}

div.modalContent>p {
    position: relative;
    bottom: 32px;
}

p.errorTitle {
    color: $redColor;
    font-size: 30px;
}

h2.subTitleModal {
    color: $beige;
    margin-top: 0;
    line-height: 93%;
    letter-spacing: -1.948px;
}

.sendBlock {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.sendBlock>button {
    width: 320px;
}

.withoutPB {
    padding-bottom: 0;
}

.withoutMB {
    margin-bottom: 0;
}

@media screen and (max-width: 1680px) {
    .content {
        margin: 0 60px 93px;
    }

    .favoriteImg {
        margin-bottom: 49px;
    }
}

@media #{$tablet} {
    .content {
        margin: 60px 17px 82px;
    }

    .text {
        font-size: 16px;
        width: 340px;
        font-style: normal;
        font-weight: 450;
        line-height: 126%; /* 20.16px */
        letter-spacing: -0.16px;
    }

    .baseTitle {
        width: 308px;
        font-size: 32px;
        font-style: normal;
        font-weight: 450;
        line-height: 110%; /* 35.2px */
        letter-spacing: -0.96px;
    }

    .text:last-of-type {
        margin-bottom: 39px;
    }

    .favorite {
        grid-template-columns: 1fr;
        margin-bottom: 28px;
    }

    .favorite img {
        top: -8px;
        margin-right: 2px;
        margin-bottom: 20px;
    }

    .assorti {
        margin-bottom: 74px;
    }

    .assorti p {
        width: 279px;
    }

    .assorti img {
        top: -8px;
        margin-right: 2px;
        
    }

    .favorite p {
        font-size: 16px;
        font-style: normal;
        font-weight: 450;
        line-height: 126%;
        /* 20.16px */
        letter-spacing: -0.16px;
    }

    .title {
        font-style: normal;
        font-weight: 450;
        line-height: 110%;
        /* 35.2px */
        letter-spacing: -0.96px;
    }

    .assorti p {
        font-size: 16px;
        font-style: normal;
        font-weight: 450;
        line-height: 126%;
        letter-spacing: -0.16px;
    }

    .check {
        max-width: 320px;
        height: 42.667px;
        text-align: center;
        font-size: 17.067px;
        font-style: normal;
        font-weight: 450;
        line-height: 17.067px;
        /* 100% */
        display: flex;
        justify-content: center;
    }

    .favoriteImg {
        margin-bottom: 45px;
    }

    .withoutMT {
        margin-top: 0;
    }

    .middleBanner {
        flex-direction: column;
        padding: 17px;
        margin-bottom: 71px;
    }

    .actionBtnContainer {
        display: flex;
        justify-content: center;
    }

    .middleBanner img {
        width: 100%;
        height: auto;
        margin: 0 0 25px;
        max-width: 322px;
    }

    .middleBanner p {
        font-size: 16px;
        line-height: 126%;
        letter-spacing: -0.16px;
        margin-bottom: 15px;
    }

    .conditions {
        margin-bottom: 92px;
        gap: 39px;
    }

    .conditions>div {
        flex-direction: column;
        align-items: start;
    }

    .conditions>div>p:first-child {
        font-size: 16px;
        line-height: 110%;
        letter-spacing: -0.32px;
        margin: 0 0 16px;
    }

    .conditions>div>p:last-child {
        font-size: 24px;
        line-height: 110%;
        letter-spacing: -0.82px;
    }

    .imgList {
        margin-bottom: 49px;
    }

    .imgList>img {
        width: 30%;
        height: auto;
    }

    .text {
        font-size: 17px;
        font-style: normal;
        font-weight: 450;
        line-height: 126%;
        letter-spacing: -0.16px;
        max-width: 358px;
    }

    .assortment {
        margin-top: 37px;
        font-size: 23.632px;
        line-height: 126%;
        letter-spacing: -0.236px;
        margin-bottom: 51px;
    }

    .assortment>a {
        font-size: 23.632px;
        line-height: 126%;
        letter-spacing: -0.236px;
    }

    .emailBlock {
        padding: 46px 17px 59px;
        margin-top: 570px;
        transform: translateY(-450px);
    }

    .emailBlock>p {
        font-size: 39px;
        line-height: 93%;
        letter-spacing: -1.17px;
        margin-bottom: 68px;
    }

    .emailBlock>div {
        flex-direction: column;
        align-items: flex-end;
    }

    .emailBlock>div>div.emailInput,
    .emailBlock>div>input.emailInput {
        padding: 0px;
        width: 100%;
    }

    .emailBlock>div>*:not(:last-child),
    div.modalContent>*:not(:last-child) {
        margin: 0 0 67px;
    }

    div.modalContent>p {
        margin-bottom: 0 !important;
    }

    .emailBlock>div>div:last-of-type {
        margin-bottom: 19px;
    }

    div.modalContent>div.emailInput,
    div.modalContent>input.emailInput {
        margin-bottom: 18px;
    }

    div.modalContent>p {
        bottom: 10px;
    }

    p.errorTitle {
        font-size: 20px;
    }

    .sendBlock>button {
        width: 190px;
    }

    .withoutPB {
        padding-bottom: 0;
    }

    .withoutMB {
        margin-bottom: 0;
    }

    .actionBtn {
        font-size: 17px;
        padding: 12px;
    }
}

@media #{$mobile} {
    .imgList {
        flex-direction: column;
    }

    .banner p {
        font-size: 38px !important;
    }

    .imgList>img {
        margin: 0 0 31px;
        width: 100%;
        max-width: 360px;
        height: auto;
    }
}