@import "styles/variables.scss";

.footerContainer {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
}

.footer {
    display: flex;
    gap: 240px;
    padding: 0 40px 60px;

    a,
    p {
        white-space: nowrap;
    }
}

.withDarkBGC {
    background-color: $darkBlue;
}

.withDarkBGC>.footer {
    padding: 60px 40px 60px;
}

.withDarkBGC>.footer:last-of-type {
    padding-top: 20px;
}

.withDarkBGC * {
    color: $beige;
}

.footer * {
    text-transform: uppercase;
}

.footer a {
    text-decoration: none;
}

.contacts {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    min-width: 132px;
}

.footer:first-of-type .contacts> :first-child {
    margin-bottom: 40px;
}

.contacts> :last-child {
    margin-top: 12px;
}

.links {
    display: flex;
    justify-content: space-between;
    width: 65vw;
}

.policy {
    justify-content: space-between;
}

.links>div {
    display: flex;
    flex-direction: column;
}

.links>div:last-child {
    align-items: flex-end;
    width: 30%;
}

.links>div> :not(:last-child) {
    margin-bottom: 12px;
}

.policy {
    justify-content: flex-start;
}

.policy>div:last-child {
    width: 33%;
}

.policy a {
    font-size: 12px;
    line-height: 140%;
    letter-spacing: -0.02em;
    text-transform: initial;
}

@media screen and (min-width: 1681px) {
    .footer {
        width: 1680px;
        margin: 0 auto;
        box-sizing: border-box;
    }

    //.links {
    //    width: 50%;
    //}
}

@media screen and (max-width: 1240px) {
    .footer {
        gap: 120px
    }

    //.links {
    //    width: 50%;
    //}
}

@media #{$tablet} {
    .footer {
        flex-direction: column;
        padding: 0 12px 40px;
        gap: 0px;

    }

    .withDarkBGC>.footer {
        padding: 40px 12px 40px;
    }

    .footer * {
        font-size: 16px;
    }

    .contacts {
        margin-bottom: 32px;
    }

    .links {
        flex-direction: column;
        gap: 16px;
        width: 100%;
    }

    .footer:first-of-type .links>div:not(:nth-child(2)) {
        margin-bottom: 32px;
    }

    .footer:first-of-type .links> :last-child {
        position: absolute;
        top: 0;
        right: 12px;
    }

    .withDarkBGC>.footer:first-of-type .links> :last-child {
        top: 40px;
    }

    .footer:last-of-type>.contacts> :first-child {
        margin-bottom: 0;
    }

    .policy {
        align-items: flex-start;
    }

    .policy>div:last-child {
        width: auto;
    }

    .footer:last-of-type .links>div:first-child {
        margin-bottom: 4px;
    }
}