@import "styles/variables.scss";

.headerContainer {
    position: sticky;
    top: 0;
    background-color: $beige;
    z-index: 1000000;
    border-bottom: 1px solid rgba(35, 35, 35, 0.3);
}

.header {
    display: flex;
    justify-content: space-between;
    position: relative;
    height: 60px;
    padding: 0 40px;
    z-index: 100000;
    box-sizing: border-box;
}

.mobileHeader {
    display: none;
    position: relative;
    z-index: 100001;
}

.banner {
	padding: 25px 15px;
	background-color: $blackColor;
	text-transform: uppercase;
	font-size: 18px;
	line-height: 1.4;
	color: #eee;
    font-family: "Suisse Int'l", "20Kopeek-Book", sans-serif;
	transition: max-height .3s linear, padding .3s linear;
	position: relative;

	@media (max-width: 767px) {
		font-size: 12px;
	}

	@media (max-width: 500px) {
		font-size: 10px;
	}

	&_hide {
		overflow: hidden;
		max-height: 0;
		padding: 0;
		padding-bottom: 0;
	}

	&_text {
		text-align: center;

		@media (max-width: 767px) {
			margin: 0 auto;
			max-width: 300px;
		}
	}
	a {
		font-size: inherit;
		font-family: inherit;
		color: currentColor;
		margin-left: 8px;
		padding-left: 8px;
		border-left: 1px solid #eee;
		&:not(:hover) {
			text-decoration: none;
		}
	}

	button {
		width: 32px;
		height: 32px;

		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		background: transparent;
		position: absolute;
		top: 50%;
		right: 15px;
		transform: translateY(-50%);

		@media (max-width: 500px) {
			right: 5px;
		}
	}
}

.white {
    background-color: $darkBlue!important;
}

@media screen and (min-width: 1681px) {
    .header {
        margin: auto;
    }
}

@media screen and (max-width: 1023px) {
    .header {
        margin: auto;
        padding: 0 20px;
    }
}


@media #{$mobile} {
    .headerContainer {
        height: 48px;
    }
    .header {
        display: none;
    }
    .mobileHeader {
        display: block;
        height: 60px;
        width: 100%;
    }
}
