@import 'styles/variables.scss';

.MobileMenuContainer {
    background-color: $darkBlue;
    position: fixed;
    top: 0;
    width: 100vw;
    height: 100vh;
    z-index: 10000;
}

.MobileMenuContainer > button {
    position: relative;
    top: 17px;
    left: 17px;
    background-color: $darkBlue;
}

.MobileMenuContainer > div:last-child {
    background-color: $darkBlue;
}

.MobileMenuContainer * {
    background-color: $darkBlue;
    color: $beige;
    fill: $beige;
}

.MobileMenuContainer button > pre,
.MobileMenuContainer button {
    background-color: transparent;
}

.logo {
    top: 4px;
    width: 70px;
}