@import "styles/variables.scss";

.logo {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    height: 60px;
}

@media #{$tablet} {
    .logo {
        height: 48px;
        margin-top: 5px;
    }
}

@media #{$mobile} {
    .logo {
        margin-top: 0px;
    }
}